<template>
    <div id='box'>
      <div class="left">
        <ul>
          <li v-for="(item, index) in stair" :key="index" :class="isact==index?'act':''" @click="isact=index;getdata(item.id)">{{item.name}}</li>
        </ul>
      </div>
      <div class="right">
        <div class="item" v-for="(item,index) in second" :key="index" @click="$router.push({path:'/erji',query:{id:item.id,second:JSON.stringify(second)}})">
          <img :src="item.image" alt="" class="img">
          <span>{{item.name}}</span>
        </div>
      </div>
    </div>
</template>
<script>
import { List  } from "vant"
import { allfenlei } from '@/axios/getData'
export default {
    components: {
      vanList: List,
    },
    props: {

    },
    data() {
        return {
          isact:0,
          stair:[],    //  一级
          second:[],   //  二级
          stair_id:null,    //  一级id
        };
    },
    created() {
      this.getdata();
    },
    methods: {
      //  性能优化  利用eval动态根据一级列表的长度 动态创建一系列数组  存放二级列表  点击请求数据时先判断值是否为空  空则请求
      async getdata(a=null){
        var res = await allfenlei({ stair_id:a});
        console.log(res.data)
        if(a==null) this.stair = res.data.stair    //  首次进入更新一级   以后不用更新
        this.second = res.data.second
        console.log(JSON.stringify(this.second))
      },
    },
    computed: {

    },
    mounted() {

    },
};
</script>

<style scoped lang="scss">
#box{
  display: flex;
}
.left{
  position: fixed;
  width:20%;
  padding:0 5pt;
  height:100%;
  overflow: scroll;
  border-right:1pt solid #DEDEDE;
  li{
    line-height: 35pt;
    text-align: center;
  }
}
.act{
  position: relative;
  color:#15BDDB;
}
.act::after{
  position: absolute;
  content:'';
  display: block;
  width:100%;
  height:2pt;
  bottom:2pt;
  left:0;
  background: #15BDDB;
}

/* 右边 */
.right{
  margin-left:25%;
  height:100%;
  overflow: scroll;
  display: flex;
  flex-wrap: wrap;
  width:100%;
  .item{
    height:auto;
    width:33.33%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:10pt 0;
    .img{
      width:50pt;
      height:50pt;
    }
    &>span{
      width:100%;
      display: inline;
      text-align: center;
      padding:3pt 0;
    }
  }
}
</style>



